<template>
  <div class="hello">
    <h1>Social</h1>
    <p>
      You can create and post your messages here.
    </p>
    <div class="input-group mb-3 w-75 mx-auto">
      <span class="input-group-text">input message</span>
      <textarea v-model="msg" class="form-control" aria-label="With textarea"></textarea>
      <button class="btn rounded btn-success mx-2" :disabled="!msg" @click="createMsg">save</button>
      <button class="btn rounded btn-danger" @click="msg=''">clear</button>
    </div>
    <h2>Posts</h2>
    <p v-if="messageList.length === 0">No messages</p>
    <ul v-else class="list-group w-75 mx-auto">
      <li v-for="msg in getMsgListReversed" :key="msg.id" class="list-group-item mx-0 mb-2 d-flex justify-content-between align-items-center">
        <textarea v-model="msg.text" class="form-control" aria-label="update text"></textarea>
        <button class="btn btn-info rounded m-2" @click="updateMsg(msg)">update</button>
        <button class="btn btn-danger rounded" @click="deleteMsg(msg)">delete</button>
      </li>
    </ul>
  </div>
</template>

<script>
import {api} from '../utils/api.js'

export default {
  name: 'HelloWorld',
  data() {
    return {
      msg: '',
      messageList: []
    }
  },
  computed: {
    getMsgListReversed() {
      return this.messageList.slice().reverse();
    }
  },
  created() {
    this.getMessageList();
  },
  methods: {
    getMessageList() {
      api.get('message/').then(res => {
        this.messageList = res.data;
        console.log(res.data);
      })
    },
    createMsg() {
      api.post('message/', JSON.stringify({text: this.msg})).then(res => {
        console.log(res.data)
        this.getMessageList()
        this.msg = ''
      })
    },
    updateMsg(msg) {
      api.put(`message/${msg.id}`, JSON.stringify(msg)).then(res => {
        console.log(res.data)
        this.getMessageList()
      })
    },
    deleteMsg(msg) {
      api.delete(`message/${msg.id}`).then(res => {
        console.log(res.data)
        this.getMessageList()
      })
    }
  }
}
</script>

